import React, {Component} from 'react';
import './App.css';
import {Form, FormControl, Button} from 'react-bootstrap';


class Stopwatch extends Component {

	constructor(props) {
		super(props);
		this.state = {time: 10}
	}

	countUntil() {
		let time = this.state.time;
		if (time === 0) {this.stopCountUntil(); alert('Countdown to 0 !')}
		this.setState({time: time - 1});
	}

	stopCountUntil() {
		clearInterval(this.intervalId);
	}

	componentDidMount() {
		this.intervalId = setInterval(() => this.countUntil(), 1000);
	}

	render() {

		return (
			<Form inline className="alert alert-success">
				<div>Count until {this.state.time}</div>
				<FormControl onChange={event => this.setState({time: event.target.value})} />
				<Button onClick={() => this.stopCountUntil()}>STOP</Button>
				<Button onClick={() => this.componentDidMount()}>CONTINUE</Button>
			</Form>

		);

	}

}

export default Stopwatch;


