import React, {Component} from 'react';
import Clock from './Clock';
import './App.css';
import {Form, FormControl, Button, Alert} from 'react-bootstrap';
import Stopwatch from './Stopwatch';

class App extends Component {

	constructor(props) {
		super(props);
		this.state = {deadline: 'December 25, 2017'}
	}

	changeDeadline() {
		//this.state.deadline = 'November 25, 2017';
		//console.log(this.state);
		this.setState({deadline: this.state.newDeadline})
	}

	render() {
		return (
			<div className="App">
				<Stopwatch defaultTime={5000} />
				<div className="App-title">Countdown to {this.state.deadline}</div>
				<Clock
					deadline={this.state.deadline}
				/>
				<Form inline>
					<FormControl className="Deadline-input" placeholder="New date..." onChange={event => this.setState({newDeadline: event.target.value})} />
					<Button onClick={() => this.changeDeadline()}>Submit</Button>
				</Form>
			</div>
		);
		
	}

}

export default App;


